<script>
import axios from 'axios';
export default {
  data(){
    return{
      price : 0,
      market_cap : 0,
      usd_24h_vol: 0,
      usd_24h_change: 0
    }
  },
  created(){
      let me = this;
      let valor = 0;
      axios.get("https://api.coingecko.com/api/v3/simple/price?ids=obic&vs_currencies=usd&include_market_cap=true&include_24hr_vol=true&include_24hr_change=true")
      .then( (res) => {
        console.log(res);
        const obic = res.data.obic
        me.price = obic.usd
        me.market_cap = obic.usd_market_cap
        me.usd_24h_vol = obic.usd_24h_vol.toFixed(2);
        me.usd_24h_change = obic.usd_24h_change.toFixed(2);
        }).catch(function(err){
          console.log(err);
        });
 
    
  }
    
  
}
</script>
